	getters: {
		works(state){
			return state.works;
		},
		worksAsAssoc(state){
			let worksAssoc = {};

			for(let i = 0; i < state.works.length; i++){
				worksAssoc[state.works[i].short_title] = state.works[i];
			}

			return worksAssoc;
		},
		getItem: (state, getters) => (name) => {
			return getters.worksAsAssoc[name];
		},
		categories(state){
			return state.categories;
		},
		itemsByCategory(state){
			let resArr = [];

			for(let i = 0; i < state.categories.length; i++){
				let one_category = {};
				let category_name = state.categories[i].sys_name;
				let category_data = state.categories[i];
				let category_items = [];
				
				for(let j = 0; j < state.works.length; j++){
					let categories_work = state.works[j].category.split(',');
					for(let k = 0; k < categories_work.length; k++){
						categories_work[k] = categories_work[k].trim();
					}
					if(categories_work.indexOf(category_name.trim()) != -1){
						category_items.push(state.works[j]);
					}
				}
				one_category["category_data"] = category_data;
				one_category["items"] = category_items;
				resArr.push(one_category);
			}
			return resArr;
		}
	},
	mutations: {

	},
	actions: {

	}

