					<div v-for="(itemCategoryData, index) in itemsByCategory"
						 class="content_tab_one" 
						 v-show="active_tab == index"
						 :key="itemCategoryData.category_data.sys_name">
						<div class="content_tab_hidden">
							<div class="block properties">
								<div class="block__h1 first_h1"></div>
								<ul class="block__desc left_text">
									<li v-for="(property, index) in itemCategoryData.category_data.list_rate[0].properties" :key="index">{{ property.name }}</li>
									<li></li>
								</ul>
							</div>
							<div v-for="(type, index) in itemCategoryData.category_data.list_rate"  :key="type.price"
							     :class="['block', hidden_offer == index ? 'active_block_price' : '' ]">
								<div class="block__h1">
									<div class="block__h1--btn_prev" @click="prevOffer()">
										<i class="fas fa-chevron-left"></i>
									</div>
									<div class="block__h1--title">{{ type.name }}</div>
									<div class="block__h1--btn_next" @click="nextOffer()">
										<i class="fas fa-chevron-right"></i>
									</div>
								</div>
								<ul class="block__desc">
									<li v-for="(type_property, index) in type.properties" :key="index">
										<template v-if="typeof(type_property.value) == 'string'">
											{{ type_property.value }}
										</template>
										<template v-if="typeof(type_property.value) == 'boolean'">
											<i v-if="type_property.value"
											   class="fas fa-check check">    	
										    </i>
										    <i v-else
									    	   class="fas fa-times cross">
											 </i>										
										</template>
									</li>
									<li>{{ type.price }} </li>
								</ul>
							</div>
						</div>
