<script>
	import {mapGetters} from 'vuex';
	export default{
		data(){
			return {
				active_tab: 0,
				hidden_offer: 0
			}
		},
		computed: {
			...mapGetters([
				'works',
				'itemsByCategory',
				'categories'
				]),
			
		},
		methods: {
			changeTabs(category_name){
				this.active_tab = category_name;
			},
			prevOffer(){
				this.hidden_offer -= 1;
				if(this.hidden_offer < 0){
					this.hidden_offer = 2;
				}
				console.log(this.hidden_offer);
			},
			nextOffer(){
				this.hidden_offer += 1;
				if(this.hidden_offer >= 3){
					this.hidden_offer = 0;
				}
				console.log(this.hidden_offer);
			}
		}
	}
</script>
<style scoped="">
	.slide-fade-enter-active {
	transition: all .3s ease;
	}
	.slide-fade-leave-active {
	transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	.slide-fade-enter, .slide-fade-leave-to
	/* .slide-fade-leave-active   2.1.8 */ {
	transform: translateX(10px);
	opacity: 0;
	}
</style>


